import React, { useState } from 'react'
import * as styles from './Sitetop.module.scss'
import ExpandableLinks from '../Buttons/ExpandableLinks'

const Sitetop = () => {
  const [activeTabIdx, setActiveTabIdx] = useState(2)

  const handleTabClick = (e) => {
    const currentTarget = e.currentTarget
    const idx = Number(currentTarget.dataset.idx)
    setActiveTabIdx(idx)
  }

  return (
    <div className={styles.wrapper}>
      <div className={styles.tabs}>
        {['', '', '/'].map((tabName, index) => (
          <button
            type="button"
            data-idx={index}
            onClick={handleTabClick}
            className={index === activeTabIdx && styles.active}
          >
            {tabName}
          </button>
        ))}
      </div>
      <div className={styles.links}>
        <a href="#" className={styles.item}>
          
        </a>
        <a href="#" className={styles.item}>
          
        </a>
        <ExpandableLinks
          links={[
            { name: '', url: '#' },
            { name: '', url: '#' },
            { name: '/', url: '#' },
            { name: '', url: '#' },
          ]}
          className={styles.item}
        />

        <a href="#" className={styles.item}>
          
        </a>
        <ExpandableLinks
          links={[
            { name: '', url: '#' },
            { name: '', url: '#' },
            { name: '', url: '#' },
          ]}
          className={styles.item}
        />
        <ExpandableLinks
          links={[
            { name: 'KOR', url: '#' },
            { name: 'ENG', url: '#' },
            { name: 'JPN', url: '#' },
            { name: 'tCHN', url: '#' },
            { name: 'sCHN', url: '#' },
          ]}
          className={styles.item}
        />
      </div>
    </div>
  )
}

export default Sitetop
